@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;600;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper {
  display: flex;
  height: 100vh;
  width: 100%;
  justify-content: center;
  align-items: center;
  background-color: #8BC6EC;
  background-image: linear-gradient(135deg, #8BC6EC 0%, #9599E2 100%);
  padding: 0px 0px 0px 0px;
  margin: 0px 0px 0px 0px;
  overflow-y: auto;
  overflow-x: hidden;
}
.login__container {
  width: 100%;
  height: 100%;
  padding: 0 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login__wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 400px;
  height: 450px;
  border: 1px solid rgb(206, 206, 206);
  border-radius: 15px;
  background-color: rgb(252, 252, 252);
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.2),
              0px 0px 10px 0px rgba(0, 0, 0, 0.2),
              0px 0px 15px 0px rgba(0, 0, 0, 0.2);
  transition: all 350ms ease-in-out 0ms;
}
.login__wrapper form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.label__input {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin-bottom: 20px;
}
.label__input:last-child {
  margin-left: auto;
  margin-bottom: 0px;
}
.label__input label {
  margin-bottom: 10px;
  font-weight: 600;
  font-family: 'Poppins', sans-serif;
}
.label__input input,
.label__input button {
  padding: 10px 10px;
  border-radius: 10px;
  font-family: 'Poppins', sans-serif;
  border: 2px solid #8BC6EC;
  transition: all 250ms ease;
}
.label__input:nth-child(1) input,
.label__input:nth-child(2) input {
  width: 200px;
}
.label__input input:focus,
.label__input button:focus {
  outline: 0;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.2),
              0px 0px 10px 0px rgba(0, 0, 0, 0.2),
              0px 0px 15px 0px rgba(0, 0, 0, 0.2);
}
.label__input:last-child button:hover {
  cursor: pointer;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.1),
              0px 0px 10px 0px rgba(0, 0, 0, 0.1),
              0px 0px 15px 0px rgba(0, 0, 0, 0.1);
}
.label__input:last-child button:hover:disabled {
  cursor: not-allowed;
}

@media screen and (max-width: 425px) {
  .login__wrapper {
    max-width: 400px;
    height: 350px;
  }
}
.main-page__wrapper {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0px 0px 0px 0px;
  margin: 0px 0px 0px 0px;
}
.header {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: auto;
  height: 60px;
  padding: 10px 20px;
  background-color: #fff;
  box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.2);
}
.container {
  width: 1200px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.container button {
  width: 200px;
  height: 45px;
  border-radius: 20px;
  border: none;
  cursor: pointer;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
  background-color:#8BC6EC;
  color: #fff;
  font-size: 18px;
  font-family: 'Poppins', sans-serif;
  transition: all 250ms ease-in-out 0ms;
}
.container button:hover {
  letter-spacing: 0.02rem;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.1),
              0px 0px 10px 0px rgba(0, 0, 0, 0.1),
              0px 0px 15px 0px rgba(0, 0, 0, 0.1);
}
.mini-nav {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.mini-nav a {
  text-decoration: none;
}
.user-login {
  color: #8BC6EC;
  font-size: 32px;
  text-transform: uppercase;
  font-weight: 800;
  font-family: 'Poppins', sans-serif;
  text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.1);
  margin-right: 10px;
  transition: all 250ms ease-in-out 0ms;
}
.icon-settings {
  position: relative;
  width: 20px;
  height: 20px;
  padding: 0px 0px 0px 0px;
  margin-left: 5px;
  cursor: pointer;
  opacity: 0.6;
  transition: all 250ms ease-in-out 0ms;
}
.icon-settings:hover {
  opacity: 1;
}
.icon-label {
  position: absolute;
  bottom: -50px;
  left: 50%;
  -webkit-transform: translateX(-45%);
          transform: translateX(-45%);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  font-weight: 400;
  color: #fff;
  border-radius: 6px;
  background-color: rgba(0, 0, 0, 0.7);
  width: 100px;
  height: 25px;
  opacity: 0;
  visibility: hidden;
  transition: all 200ms ease-in 0ms;
}
.icon-label:before {
  content: '';
  position: absolute;
  border: 8px solid transparent; 
  border-bottom: 8px solid rgba(0, 0, 0, 0.7);
  top: -16px;
}
.icon-settings:hover .icon-label {
  opacity: 1;
  visibility: visible; 
  bottom: -40px;
}

.body {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 30px;
}
.body h1 {
  color: #fff;
  font-size: 64px;
  text-transform: uppercase;
  text-align: center;
  font-family: 'Poppins', sans-serif;
}

.newChannel {
  position: fixed;
  padding: 0 20px;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.89);
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: all 450ms ease;
  opacity: 0;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}
.newChannel.show {
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

.newChannel h2 {
  font-size: 36px;
  color: #fff;
  font-weight: 800;
  font-family: 'Poppins', sans-serif;
  text-align: center;
  text-transform: uppercase;
}
.newChannel form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.newChannel form input {
  width: 420px;
  box-sizing: border-box;
  padding: 10px 20px;
  border-radius: 20px;
  font-size: 18px;
  color: #333;
  font-weight: 400;
  font-family: 'Poppins', sans-serif;
  margin-bottom: 20px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.2),
              0px 0px 10px 0px rgba(0, 0, 0, 0.2),
              0px 0px 15px 0px rgba(0, 0, 0, 0.2);
  outline: 0;
  border: none;
  transition: all 250ms ease;
}
.newChannel form input:last-child {
  margin-bottom: 0;
  cursor: pointer;
  color: #fff;
  background-color:#8BC6EC;
}
.newChannel form input:last-child:hover {
  -webkit-transform: translateY(3px);
          transform: translateY(3px);
  letter-spacing: 0.03rem;
  background-color:#5fa4d3;
}
.newChannel form input:last-child:hover:disabled {
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
  letter-spacing: 0rem;
  background-color:#8BC6EC;
  cursor: not-allowed;
}

@media screen and (max-width: 425px) {
  .user-login {
    display: none;
  }
  .body h1 {
    font-size: 48px;
  }
  .newChannel form input {
    width: 300px;
  }
}
*, 
*:before,
*:after {
  padding: 0px 0px 0px 0px;
  margin: 0px 0px 0px 0px;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}
.new-channel__wrapper {
  width: 100%;
  height: 100vh;
  padding: 0 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #4158D0;
  background-image: linear-gradient(43deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%);
  transition: all 650ms ease-in 0ms;
}
.new-channel__body {
  position: fixed;
  top: 50%;
  left: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: #FFCC70;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.2),
              0px 0px 10px 0px rgba(0, 0, 0, 0.2),
              0px 0px 15px 0px rgba(0, 0, 0, 0.2);
  border-radius: 20px;
  width: 900px;
  height: 650px;
  padding: 20px 40px;
}
.new-channel__header {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: auto;
}
.title-block h2 {
  font-size: 36px;
  font-weight: 800;
  color: #fff;
}
.title-block span {
  display: inline-block;
  font-size: 18px;
  font-weight: 400;
  color: #fff;
  width: 500px;
}

.logout__btn {
  width: 200px;
  height: 45px;
  border-radius: 20px;
  border: none;
  cursor: pointer;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
  background-color:#8BC6EC;
  color: #fff;
  font-size: 18px;
  transition: all 250ms ease-in-out 0ms;
}
.logout__btn:hover {
  letter-spacing: 0.02rem;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.1),
              0px 0px 10px 0px rgba(0, 0, 0, 0.1),
              0px 0px 15px 0px rgba(0, 0, 0, 0.1);
}

.chat__body {
  background-color: rgb(240, 240, 240);
  border: 1px solid #fff;
  border-radius: 20px;
  height: 360px;
  padding-top: 10px;
  width: 100%;
  overflow-y: hidden;
}
.chat__body::-webkit-scrollbar {
  width: 0;
}
.user__message {
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 0 25px;
}
.echo__message {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 0 25px;
}
.user__avatar {
  align-items: center;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  height: 45px;
  overflow: hidden;
  width: 45px;
}
.user__avatar img {
  height: 100%;
  object-fit: cover;
}
.text__block {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  margin-left: 15px;
}
.user__message .text__block {
  align-items: flex-end;
  margin: 0 15px 0 0;
}
.user__name {
  color: #609fc9;
  font-weight: 600;
  font-size: 16px;
  line-height: 100%;
}
.user__messages {
  color: #000;
  font-weight: 200;
  font-size: 18px;
  line-height: 140%;
  letter-spacing: 0.04rem;
  padding-top: 10px;
}

.chat__footer {
  margin-top: 20px;
  width: 100%;
  height: 60px;
  display: flex;
  overflow: hidden;
  border-radius: 20px;
}
.chat__footer textarea {
  flex: 0 1 80%;
  resize: none;
  padding: 10px 20px;
  border: none;
  outline: 0;
}
.chat__footer button {
  flex: 0 1 20%;
  border: none;
  outline: 0;
  background-color:#8BC6EC;
  color: #fff;
  font-size: 18px;
  transition: all 250ms ease-in-out 0ms;
}
.chat__footer button:hover {
  background-color:#609fc9;
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .new-channel__body {
    width: 700px;
  }
  .title-block span {
    width: 100%;
  }
}
@media screen and (max-width: 425px) {
  .new-channel__body {
    width: 400px;
  }
  .new-channel__header {
    flex-direction: column;
    align-items: center;
  }
  .title-block span {
    width: 100%;
  }
  .logout__btn {
    margin-top: 20px;
  }
}
@media screen and (max-width: 375px) {
  .new-channel__body {
    width: 350px;
  }
}
@media screen and (max-width: 320px) {
  .new-channel__body {
    width: 300px;
  }
}
.settings__container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 0 20px;
  transition: all 350ms ease-in-out 0ms;
}
.settings__wrapper {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  width: 430px;
  height: 250px;
  padding: 20px;
  border-radius: 20px;
  background-color: rgb(235, 235, 235);
  box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.1),
              0px 0px 6px 0px rgba(0, 0, 0, 0.1),
              0px 0px 20px 0px rgba(0, 0, 0, 0.1);
}
.settings__wrapper h2 {
  position: absolute;
  top: 0;
  -webkit-transform: translateY(-70px);
          transform: translateY(-70px);
  text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
  font-size: 42px;
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 0.3rem;
  color: #fff;
}
.col-1 {
  flex: 0 1 10%;
  display: flex;
  
}
.col-2 {
  flex: 0 1 80%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-end;
}
.avatar {
  overflow: hidden;
  min-width: 140px;
  height: 140px;
  border-radius: 30px;
  box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.2),
              0px 0px 8px 0px rgba(0, 0, 0, 0.2),
              0px 0px 14px 0px rgba(0, 0, 0, 0.2);
}
.avatar img {
  width: 100%;
  object-fit: cover;
}
.col-2 .labelInput {
  margin-bottom: 10px;
}
.col-2 .labelInput label {
  margin-bottom: 5px;
}
.input__setting {
  width: 200px;
  height: 35px;
  padding: 10px;
  border-radius: 10px;
  font-family: 'Poppins', sans-serif;
  border: 2px solid #8BC6EC;
  transition: all 250ms ease;
}
.apply-btn {
  display: inline-block;
  width: 60px;
  height: 35px;
  border-radius: 10px;
  line-height: 35px;
  color: #fff;
  text-transform: uppercase;
  font-family: 'Poppins', sans-serif;
  border: 2px solid #8BC6EC;
  background-color: #8BC6EC;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.1),
              0px 0px 10px 0px rgba(0, 0, 0, 0.1),
              0px 0px 15px 0px rgba(0, 0, 0, 0.1);
  transition: all 250ms ease;
}
.input__setting:focus,
.col-2 button:focus {
  outline: 0;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.2),
              0px 0px 10px 0px rgba(0, 0, 0, 0.2),
              0px 0px 15px 0px rgba(0, 0, 0, 0.2);
}
.col-2 button:hover {
  cursor: pointer;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.2),
              0px 0px 10px 0px rgba(0, 0, 0, 0.2),
              0px 0px 15px 0px rgba(0, 0, 0, 0.2);
}
.col-2 button:disabled {
  color: rgb(231, 231, 231);
  cursor: not-allowed;
  background-color: #8bc5ec69;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0),
              0px 0px 10px 0px rgba(0, 0, 0, 0),
              0px 0px 15px 0px rgba(0, 0, 0, 0);
}

@media screen and (max-width: 425px) {
  .settings__wrapper h2 {
    font-size: 36px;
  }
  .settings__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 385px;
  }
  .col-1 {
    flex: 0 1 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .avatar {
    width: 120px;
    height: 120px;
    margin-bottom: 20px;
  }

}
@media screen and (max-width: 375px) {
  .settings__wrapper {
    height: 385px;
  }
  .settings__wrapper h2 {
    font-size: 28px;
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
  }
}
